import React from 'react';
import HyperModal from 'react-hyper-modal';
import Gist from 'react-gist';
import BaseLayout from 'components/BaseLayout';
import Button from 'components/Button';
import HeaderText from 'components/HeaderText';
import './style.scss';

class HyperModalComponent extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isModalOpen: false,
    };
  }

  openModal = () => this.setState({ isModalOpen: true });
  closeModal = () => this.setState({ isModalOpen: false });

  render() {
    return (
      <BaseLayout className="example-hyper-modal-wrapper">
        <header className="header">
          <div className="title">react hyper modal</div>
        </header>
        <div className="content">
          <HyperModal
            isOpen={this.state.isModalOpen}
            requestClose={this.closeModal}
            classes={{
              contentClassName: 'modal-content',
            }}
          />
          <HeaderText title="Simple modal" />
          <Gist id="965ebd30f423f14ac342ba6be07fe54b" />
          <Button
            onClick={this.openModal}
            title="open simple modal"
            position="center"
            wide
          />
        </div>
      </BaseLayout>
    );
  }
}

export default HyperModalComponent;
